"use client";

import React from "react";
import Image from "next/image";
import { MdLogout } from "react-icons/md";

export const Header = () => {
  return (
    <>
      <header className="flex justify-start px-10 py-3 items-center w-full gap-5">
        <Image
          src="/assets/ticket.png"
          width={45}
          height={200}
          alt="logo"
          unoptimized
        />
        <h1 className="text-2xl font-semibold font-montserrat text-blue-900">
          HelpDesk
        </h1>

        <div className="ml-auto">
          <button className="w-8 flex items-center">
            <MdLogout className="w-full text-gray-700 h-full hover:text-red-800 transition-all" />
          </button>
        </div>
      </header>

      <div></div>
    </>
  );
};
