import React, { useState } from "react";
import { Modal } from "../modal/page";
import { TicketInfoRow } from "../tr/page";

export const TicketLayout = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [tickets, setTickets] = useState<
    Array<{ id: number; title: string; description: string; status: string }>
  >([]);

  const fetchTickets = async () => {
    try {
      const response = await fetch("http://localhost:3001/tickets");
      const data = await response.json();
      setTickets(data);
    } catch (error) {
      console.error("Erro ao buscar chamados:", error);
    }
  };

  const addTicketToTable = (newTicket: {
    id: number;
    title: string;
    description: string;
    status: string;
  }) => {
    setTickets((prevTickets) => [...prevTickets, newTicket]);
  };

  React.useEffect(() => {
    fetchTickets();
  }, []);

  return (
    <div className="px-10 py-6 flex flex-col">
      <div className="flex justify-between items-center">
        <h1 className="font-montserrat text-2xl font-semibold text-gray-800">
          Chamados
        </h1>
        <button
          className="px-4 py-2 bg-cyan-600 text-white rounded-md hover:bg-cyan-700 transition"
          onClick={() => setIsModalOpen(true)}
        >
          New Desk
        </button>
      </div>
      <table className="table-auto border-collapse w-full mt-4 border border-gray-300">
        <thead>
          <tr className="bg-gray-100 text-gray-800">
            <th className="border border-gray-300 px-4 py-2">ID</th>
            <th className="border border-gray-300 px-4 py-2">Título</th>
            <th className="border border-gray-300 px-4 py-2">Descrição</th>
            <th className="border border-gray-300 px-4 py-2">Status</th>
          </tr>
        </thead>
        <tbody>
          {tickets.map((ticket) => (
            <TicketInfoRow
              key={ticket.id}
              id={ticket.id}
              title={ticket.title}
              description={ticket.description}
              status={ticket.status}
            />
          ))}
        </tbody>
      </table>
      {isModalOpen && (
        <Modal
          setIsModalOpen={setIsModalOpen}
          onTicketCreated={addTicketToTable}
        />
      )}
    </div>
  );
};
